import * as React from "react";
import {useState} from "react";
import IconButton from "@mui/material/IconButton";
import {ListAlt} from "@mui/icons-material";
import Container from "@mui/material/Container";
import FormControl from "@mui/material/FormControl";
import FormLabel from "@mui/material/FormLabel";
import FormGroup from "@mui/material/FormGroup";
import FormControlLabel from "@mui/material/FormControlLabel";
import Switch from "@mui/material/Switch";
import SwipeableDrawer from "@mui/material/SwipeableDrawer";
import dictionary from "../lang/dictionary.json"

export function VerboSettings({initVerbos, onClose}) {

  const [isSettingsOpen, setSettingsOpen] = useState(false)
  const [selectedVerbos, setSelectedVerbos] = useState(initVerbos)

  function switchEnabledVerbo(verbo) {
    setSelectedVerbos(selected => {
      if (selected.includes(verbo)) {
        if (selected.length > 1) {
          return selected.filter(wrd => wrd !== verbo)
        } else {
          return selected
        }
      } else {
        return selected.concat(verbo)
      }
    })
  }

  return (
    <IconButton>
      <ListAlt fontSize="large" color="secondary" onClick={() => setSettingsOpen(true)}/>
      <SwipeableDrawer
        anchor="right"
        open={isSettingsOpen}
        onClose={() => {
          setSettingsOpen(false)
          onClose(selectedVerbos)
        }}
      >
        <Container component="main" maxWidth="xs">
          <FormControl component="fieldset" variant="standard">
            <FormLabel component="legend">Verbos</FormLabel>
            <FormGroup>
              {
                Object.keys(dictionary).sort().map(verbo => {
                  return <FormControlLabel
                    control={
                      <Switch checked={selectedVerbos.includes(verbo)}
                              onChange={() => switchEnabledVerbo(verbo)}
                      />
                    }
                    label={verbo}
                  />
                })
              }
            </FormGroup>
          </FormControl>
        </Container>
      </SwipeableDrawer>
    </IconButton>

  );
}