import * as React from 'react';
import {useState} from 'react';
import Button from '@mui/material/Button';
import CssBaseline from '@mui/material/CssBaseline';
import Grid from '@mui/material/Grid';
import Typography from '@mui/material/Typography';
import Container from '@mui/material/Container';
import {createTheme, ThemeProvider} from '@mui/material/styles';
import {Copyright} from "./components/Copyright";
import {Toolbar} from "@mui/material";
import {conjugar, getVocab, Group} from "./lang/grammatica";
import {shuffleArray} from "./util/util";
import {GroupSettings} from "./components/GroupSettings";
import {ExerciseCard} from "./components/ExerciseCard";
import {ResultCard} from "./components/ResultCard";
import {VerboSettings} from "./components/VerboSettings";
import {MessageCard} from "./components/MessageCard";

const theme = createTheme();


const personas = ['Yo', 'Tu', 'El', 'Nosotros', 'Vosotros', 'Ellos']

function randomCombinations(groups, verbos) {
  const combinations = []
  groups.forEach(group => {
    const vocab = getVocab(group, verbos)
    for (let verbo of vocab) {
      for (let index in personas) {
        combinations.push([group, Number(index), verbo])
      }
    }
  })
  shuffleArray(combinations)
  return combinations
}


const settingsGroupsVersion = 2
const settingGroupsName = "settingsGroups"

function loadSettingGroups() {
  const settingsGroups = JSON.parse(localStorage.getItem(settingGroupsName))
  if (settingsGroups?.version === settingsGroupsVersion) {
    return settingsGroups.groups
  } else {
    return [Group.PRESENTE_AR.key]
  }
}

function storeSettingGroups(groups) {
  localStorage.setItem(settingGroupsName, JSON.stringify({version: settingsGroupsVersion, groups: groups}))
}

const settingsVerbosVersion = 1
const settingVerbosName = "settingsVerbos"

function loadSettingVerbos() {
  const settingsVerbos = JSON.parse(localStorage.getItem(settingVerbosName))
  if (settingsVerbos?.version === settingsVerbosVersion) {
    return settingsVerbos.verbos
  } else {
    return ["ser"]
  }
}

function storeSettingVerbos(verbos) {
  localStorage.setItem(settingVerbosName, JSON.stringify({version: settingsVerbosVersion, verbos: verbos}))
}


export default function App() {

  const [state, setState] = useState(() => nextState({
    groups: loadSettingGroups(), verbos: loadSettingVerbos(), combinations: [], startTimes: [], endTimes: []
  }))
  const [hint, setHint] = useState('')

  function nextState(currState, updatedGroups, updatedVerbos) {
    const groups = updatedGroups || currState.groups
    const verbos = updatedVerbos || currState.verbos
    let combinations = [...currState.combinations]
    let startTimes = [...currState.startTimes]
    let endTimes = [...currState.endTimes]
    let total = currState.total
    if (endTimes.length < startTimes.length) {
      endTimes.push(Date.now())
    }

    if (combinations.length === 0 && currState.result === "GAME") {
      return {
        ...currState,
        groups: groups,
        verbos: verbos,
        startTimes: startTimes,
        endTimes: endTimes,
        total: total,
        result: "OK"
      }
    }

    if (combinations.length === 0 || updatedGroups || updatedVerbos) {
      storeSettingGroups(groups)
      storeSettingVerbos(verbos)
      combinations = randomCombinations(groups, verbos)
      total = combinations.length
      startTimes = []
      endTimes = []
    }

    if (combinations.length === 0) {
      return {
        ...currState,
        groups: groups,
        verbos: verbos,
        startTimes: startTimes,
        endTimes: endTimes,
        total: total,
        result: "NO_COMB",
      }
    }

    const [group, persona, verbo] = combinations.pop()
    startTimes.push(Date.now())
    return {
      groups: groups,
      verbos: verbos,
      combinations: combinations,
      tiempo: Group[group].tiempo,
      verbo: verbo,
      persona: persona,
      startTimes: startTimes,
      endTimes: endTimes,
      total: total,
      result: "GAME",
    }
  }

  function findHintFor(state) {
    return conjugar(state.tiempo, state.verbo, state.persona)
  }

  function onClickNext() {
    setHint('')
    setState(nextState)
  }

  function onClickHint() {
    setHint(findHintFor(state))
    setState(currState => {
      const endTimes = [...currState.endTimes]
      endTimes.push(Date.now())
      return {
        ...currState,
        endTimes: endTimes
      }
    })
  }

  function getProgress() {
    return state.total - state.combinations.length + '/' + state.total
  }

  function getExercise() {
    if (hint) {
      return hint
    } else {
      return state.verbo
    }
  }

  function getAverageTime() {
    const times = state.startTimes.map((startTime, idx) => {
      return state.endTimes[idx] - startTime
    })
    const sum = times.reduce((a, b) => a + b, 0);
    const result = (sum / times.length) || 0
    return Math.round(result);
  }

  function onGroupSettingsClose(groups) {
    setState(currState => {
      if (currState.groups !== groups) {
        return nextState(currState, groups, null)
      } else {
        return currState
      }
    })
    setHint('')
  }

  function onVerboSettingsClose(verbos) {
    setState(currState => {
      if (currState.verbos !== verbos) {
        return nextState(currState, null, verbos)
      } else {
        return currState
      }
    })
    setHint('')
  }

  function getCard() {
    if (state.result === "OK") {
      return <ResultCard averageTime={getAverageTime()}/>
    } else if (state.result === "NO_COMB") {
      return <MessageCard message={"Elige otros verbos o tiempos"}/>
    } else {
      return <ExerciseCard exercise={getExercise()} hint={hint} persona={personas[state.persona]}
                           progress={getProgress()} tiempo={state.tiempo}/>;
    }
  }

  function getGroups() {
    return state.groups
  }

  function getVerbos() {
    return state.verbos
  }

  return (
    <ThemeProvider theme={theme}>
      <Container component="main" maxWidth="xs">
        <CssBaseline/>
        <Toolbar disableGutters sx={{borderBottom: 1, borderColor: 'divider'}}>
          <Typography
            variant="h5"
            color="inherit"
            align="left"
            sx={{flex: 1}}
          >
            Conjugador
          </Typography>
          <VerboSettings initVerbos={getVerbos()} onClose={(verbos) => onVerboSettingsClose(verbos)}/>
          <GroupSettings initGroups={getGroups()} onClose={(groups) => onGroupSettingsClose(groups)}/>
        </Toolbar>
        {getCard()}
        <Grid container spacing={2} sx={{mt: 1}}>
          <Grid item xs={5}>
            <Button type="submit" fullWidth variant="contained" onClick={onClickHint}>EnseÃ±ar</Button>
          </Grid>
          <Grid item xs={7}>
            <Button type="submit" fullWidth variant="contained" onClick={onClickNext}>Siguiente</Button>
          </Grid>
        </Grid>
        <Copyright sx={{mt: 4, mb: 4}}/>
      </Container>
    </ThemeProvider>
  );
}